import { Fragment, useCallback, useEffect, useState } from 'react';
import { useMedia } from 'react-use';
import { PageTitle, H2, H5, H6 } from '../../components/common/Typography';
import AlertModal from '../../components/Modals/AlertModal';
import MDStatsModal from '../../components/Modals/MDStatsModal';
import {
  PageWrapper,
  HeaderWrapper,
  WalletTokenView,
  CollectCardRow,
  CollectionWrapper,
  Button,
  StatusDotPanel,
  StatusDot,
  MDStatsButton,
  HeaderLeft
} from './MyWallet.styled';
import { useWalletConnect } from '../../hooks/WalletConnect';
import CollectCard from './CollectCard';
import Slick, { Settings } from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import axios from 'axios';

interface NFTAssets {
  asset: string;
  image: string;
  name: string;
  rank?: string;
}

interface NFTDataProps {
  name: string;
  id: string;
  data: NFTAssets[];
}

const policyIdData = [
  {
    name: 'Mad Dog Car Club',
    id: '7e605894cdcbe8d1933f0a8760d93bd121aca583cf21ad1c25e51fd3'
  },
  {
    name: 'Mad Dog Car Collection',
    id: '0414065c7a2bbf91b876449384ba68a17aec83e8e08ab919262225ed'
  },
  {
    name: 'MD License',
    id: '757d6d0b72beba043054f920156b396db16e4f2492c6cec089ebd608'
  },
  {
    name: 'Baby Alien Club ',
    id: '15509d4cb60f066ca4c7e982d764d6ceb4324cb33776d1711da1beee'
  },
  {
    name: 'Bad Fox Motorcycle Club',
    id: 'fa669150ad134964e86b2fa7275a12072f61b438d0d44204d3a2f967'
  },
  {
    name: 'ThoseAMA',
    id: '94a0f0ec45d2347dcc4fb364ab7432f4e15c38cc0af1787b1a60e401'
  },
  {
    name: 'Claypez',
    id: 'bd30c07bed903332937dcf328e2bd6da5aec1e686c13e8c5f2c99e48'
  },
  {
    name: 'Ape Nation',
    id: '58b9f55e6ea9828dea7a8d9f49420171c6360f99b5e6e86de5fdb644'
  },
  {
    name: 'Space Ape Club	',
    id: 'ee6da4b71e0913cbebec02edc23653f9b970af69324fdddfed1285d9'
  },
  {
    name: 'Virtua',
    id: '3956f6646614ee82a16b9e636d5cd3d54c09fe20389c9615f5b06c1e'
  },
  {
    name: 'Disco Solaris',
    id: 'd0112837f8f856b2ca14f69b375bc394e73d146fdadcc993bb993779'
  },
  {
    name: 'Salty Seagulls Society',
    id: '5329a9b87459e1df8e86ce56bae4b3770862c0969011e6faf474b65f'
  }
];

const splitArrayToChunks = (arr: Array<any>, size: number) => {
  const max = Math.ceil(arr.length / size);
  const result = [];
  for (let i = 0; i < max; i++) {
    result.push(arr.slice(i * size, (i + 1) * size));
  }
  return result;
};

const settings: Settings = {
  dots: true,
  infinite: false,
  lazyLoad: 'ondemand',
  speed: 500,
  slidesToShow: 1,
  slidesToScroll: 1
};

// let flag = false;

const MyWalletPage = () => {
  const [tokenAmount, setTokenAmount] = useState(0);
  const [totalAccumulated, setTotalAccumulated] = useState(0);
  const [nftData, setNftData] = useState<NFTDataProps[] | []>([]);
  const { myWalletAddress, lucid, accumulating } = useWalletConnect();
  const [alertMessage, setAlertMessage] = useState('');
  const [showAccumlating, setShowAccumulating] = useState(false);
  const isMobile = useMedia('(max-width: 425px)');
  const isMobileHeader = useMedia('(max-width: 768px)');
  const [showMdStateModal, setShowMdStateModal] = useState(false);
  const [mdState, setMdState] = useState();

  const fetchTotalAccumulated = useCallback(async () => {
    if (myWalletAddress && accumulating) {
      const { data } = await axios.get(
        `https://bobbmv5hi5.execute-api.eu-west-2.amazonaws.com/default/MDCC-totalRewardsREST?address=${myWalletAddress}`
      );

      setTotalAccumulated(data.rewards / 100000000);
    }
  }, [myWalletAddress, accumulating]);

  const fetchMDTokens = useCallback(async () => {
    if (myWalletAddress && accumulating) {
      const { data } = await axios.get(
        `https://h9fxd002x6.execute-api.eu-west-2.amazonaws.com/default/getMDCC-tokensInWallet?address=${myWalletAddress}`
      );

      setTokenAmount(data.amount / 100000000);
    }
  }, [myWalletAddress, accumulating]);

  const getAllCollections = useCallback(async () => {
    if (myWalletAddress) {
      const basicData = [...nftData];

      policyIdData.map(async (item) => {
        let page = 1,
          data = {};
        let arr: NFTAssets[] = [];
        do {
          data = {};
          data = await axios.get(
            `https://brefcjavvi.execute-api.eu-west-2.amazonaws.com/default/MDDC-getNFTs-all?address=${myWalletAddress}&page=${page}&policy=${item.id}`
          );

          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          data = data.data;
          if (Object.values(data).length) {
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            arr = [...arr, ...Object.values(data)];

            if (basicData.length) {
              if (basicData.find((nftItem: NFTDataProps) => nftItem.id == item.id)) {
                // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                // @ts-ignore
                basicData.find((nftItem: NFTDataProps) => nftItem.id == item.id).data = [...arr];

                // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                // @ts-ignore
                setNftData([...basicData]);
              } else {
                const obj: NFTDataProps = {
                  name: item.name,
                  id: item.id,
                  data: arr
                };
                // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                // @ts-ignore
                basicData.push(obj);
                setNftData([...basicData]);
              }
            } else {
              const obj: NFTDataProps = {
                name: item.name,
                id: item.id,
                data: arr
              };
              // eslint-disable-next-line @typescript-eslint/ban-ts-comment
              // @ts-ignore
              basicData.push(obj);
              setNftData([...basicData]);
            }
          }
          page++;
        } while (Object.values(data).length);
      });
    }
  }, [myWalletAddress]);

  const handleRefresh = async () => {
    getAllCollections();
    fetchMDTokens();
    fetchTotalAccumulated();
    if (accumulating) {
      const hasUnstaked = await axios.get(
        `https://z840g8yu3l.execute-api.eu-west-2.amazonaws.com/default/MDCC-hasUnstaked?address=${myWalletAddress}`
      );
      if (hasUnstaked.data.rewards) {
        if (lucid) {
          try {
            const tx = await lucid
              .newTx()
              .payToAddress('addr1vxcv899n9w76ca6s83kfkxw2yadeha7rqh3vwzzyu5004lqng6snf', {
                lovelace: 2500000n
              })
              .complete();
            const signedTx = await tx.sign().complete();

            const txHash = await signedTx.submit();
            setAlertMessage('New Assets have began Accumulation');
            setShowAccumulating(true);
          } catch (err) {
            setAlertMessage('Transaction was unsuccessful');
            setShowAccumulating(true);
          }
        }
      } else {
        setAlertMessage('All Assets are Accumulating');
        setShowAccumulating(true);
      }
    } else {
      setAlertMessage('You are not accumulating!');
      setShowAccumulating(true);
    }
  };

  const getMDState = async () => {
    const { data } = await axios.get(
      'https://2mx8snzj46.execute-api.eu-west-2.amazonaws.com/default/MDCC-getWalletStats?address=' +
        myWalletAddress
    );
    setMdState(data);
  };

  useEffect(() => {
    if (myWalletAddress) {
      getAllCollections();
      getMDState();
      if (accumulating) {
        fetchMDTokens();
        fetchTotalAccumulated();
      }
    }
  }, [myWalletAddress, accumulating]);

  return (
    <div className="page">
      <PageWrapper>
        <HeaderWrapper className="title-panel">
          <HeaderLeft>
            <PageTitle textAlign="left" className="page-title d-flex align-items-center">
              MY WALLET
              <Button className="btn p-0 border-0 outline-0" onClick={handleRefresh}>
                <img src="/assets/images/refresh.svg" alt="refresh-icon" />
              </Button>
            </PageTitle>
            <MDStatsButton onClick={() => setShowMdStateModal(true)}>MD STATS</MDStatsButton>
          </HeaderLeft>
          {isMobileHeader ? (
            ''
          ) : (
            <StatusDotPanel>
              <H6 fontWeight={700} className="d-flex align-items-center">
                <StatusDot />
                EARNING
              </H6>
              <H6 fontWeight={700} className="d-flex align-items-center">
                <StatusDot className="unstacked" />
                NOT EARNING
              </H6>
            </StatusDotPanel>
          )}
        </HeaderWrapper>
        <HeaderWrapper>
          <WalletTokenView>
            <H5 className="amount-title">TOTAL ACCUMULATED:</H5>
            <H2 className="amount-value">{totalAccumulated.toFixed(8)}</H2>
          </WalletTokenView>
          <WalletTokenView>
            <H5 className="amount-title">TOTAL MD TOKENS:</H5>
            <H2 className="amount-value">{tokenAmount.toFixed(8)}</H2>
          </WalletTokenView>
          {isMobileHeader ? (
            <StatusDotPanel>
              <H6 fontWeight={700} className="d-flex align-items-center">
                <StatusDot />
                EARNING
              </H6>
              <H6 fontWeight={700} className="d-flex align-items-center">
                <StatusDot className="unstacked" />
                NOT EARNING
              </H6>
            </StatusDotPanel>
          ) : (
            ''
          )}
        </HeaderWrapper>
        {policyIdData.map((policyID, index) => {
          const data = nftData.find((nftItem) => nftItem.id == policyID.id);
          if (data)
            return (
              <CollectionWrapper key={index}>
                {/* eslint-disable-next-line @typescript-eslint/ban-ts-comment  */}
                {/* @ts-ignore */}
                <H5>POLICY ID: {data.name}</H5>
                {/* eslint-disable-next-line @typescript-eslint/ban-ts-comment  */}
                {/* @ts-ignore */}
                <H6 className="policy-id">{data.id}</H6>
                <div className="mt-5">
                  <Slick {...settings}>
                    {/* eslint-disable-next-line @typescript-eslint/ban-ts-comment  */}
                    {/* @ts-ignore */}
                    {splitArrayToChunks(data.data, isMobile ? 4 : 8).map((chunk, idx) => (
                      <div key={idx}>
                        <CollectCardRow>
                          {chunk.map((item, id) => {
                            let imageId: string;
                            let stackStatus = item.staked;
                            let amount;
                            if (item.rewardsAccumulated) {
                              amount = (+item.rewardsAccumulated / 100000000).toFixed(1);
                            }
                            if (typeof item.image == 'object') {
                              // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                              // @ts-ignore
                              imageId = item.image[0].replaceAll('ipfs://', '');
                            } else {
                              imageId = item.image.replaceAll('ipfs://', '');
                            }
                            const name = item.name.replaceAll(policyID.name, '');
                            let rank: string;
                            if (item.rank == 'rank not available') {
                              rank = '';
                            } else {
                              rank = item.rank;
                            }
                            if (policyID.name == 'Mad Dog Car Collection') {
                              if (
                                item.asset.includes('43686173736973') ||
                                item.asset.includes('456e67696e65') ||
                                item.asset.includes('576865656c')
                              ) {
                                stackStatus = false;
                              }
                            }
                            return (
                              <CollectCard
                                key={id}
                                src={`https://gateway.ipfs.io/ipfs/${imageId}`}
                                name={name}
                                rank={rank}
                                stackStatus={stackStatus}
                                amount={amount}
                              />
                            );
                          })}
                        </CollectCardRow>
                      </div>
                    ))}
                  </Slick>
                </div>
              </CollectionWrapper>
            );
          else return <Fragment key={index}></Fragment>;
        })}
      </PageWrapper>
      <AlertModal
        show={showAccumlating}
        onClose={() => setShowAccumulating(false)}
        message={alertMessage}
      />
      <MDStatsModal
        show={showMdStateModal}
        onClose={() => setShowMdStateModal(false)}
        data={mdState}
      />
    </div>
  );
};

export default MyWalletPage;
