import styled from 'styled-components';

export const PageWrapper = styled.div`
  max-width: 1100px;
  margin: 0 auto;

  @media screen and (max-width: 768px) {
    margin-top: 60px;
    padding: 0 35px;
    .page-title {
      font-size: 36px;
    }
  }

  @media screen and (max-width: 550px) {
    padding: 0;
    .page-title {
      justify-content: center;
    }
    .slick-prev {
      left: -15px;
    }
    .slick-next {
      right: -15px;
    }
  }
`;

export const Button = styled.button`
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #e2ae39;
  margin-left: 20px;
  img {
    width: 100%;
  }
  &:hover {
    background-color: #e2ae39;
  }
  @media screen and (max-width: 425px) {
    width: 30px;
    height: 30px;
    margin-left: 15px;
  }
`;

export const HeaderWrapper = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding-bottom: 48px;
  border-bottom: 1px solid #fff;
  margin-bottom: 110px;

  &.title-panel {
    border-bottom: 0;
    align-items: center;
    padding-bottom: 0;
    h1 {
      margin-bottom: 0;
    }
  }
  @media screen and (max-width: 768px) {
    flex-flow: column;
    align-items: center;
    margin-bottom: 50px;
  }

  @media screen and (max-width: 425px) {
    margin-bottom: 44px;
  }
`;

export const StatusDotPanel = styled.div`
  padding: 21px;
  border: 1px solid #ffffff;
  border-radius: 10px;
  h6 {
    font-size: 21px;
    line-height: 1;
    &:first-of-type {
      margin-bottom: 5px;
    }
  }
  @media screen and (max-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 328px;
    h6 {
      &:first-of-type {
        margin-bottom: 0px;
      }
    }
  }
  @media screen and (max-width: 425px) {
    width: 295px;
    h6 {
      font-size: 16px;
    }
  }
`;

export const StatusDot = styled.div`
  display: inline-block;
  margin-right: 12px;
  width: 22px;
  height: 22px;
  min-width: 22px;
  min-height: 22px;
  border-radius: 50%;
  background-color: #00ff38;
  &.unstacked {
    background-color: #ff0000;
  }
  @media screen and (max-width: 768px) {
    width: 11px;
    height: 11px;
    min-width: 11px;
    min-height: 11px;
  }
`;

export const WalletTokenView = styled.div`
  padding: 25px 40px;
  background: #e2ae39;
  border-radius: 10px;

  .amount-title {
    color: #fff;
    font-weight: 700;
    font-size: 32px;

    @media screen and (max-width: 991px) {
      font-size: 24px;
    }
  }

  .amount-value {
    font-size: 64px;
    color: #000;
    margin: 0;
    text-align: center;
    font-weight: 700;

    @media screen and (max-width: 991px) {
      font-size: 36px;
    }
  }
  @media screen and (max-width: 991px) {
    width: 328px;
  }
  @media screen and (max-width: 768px) {
    padding: 21px 12px;
    text-align: center;
    margin-bottom: 30px;
  }
  @media screen and (max-width: 425px) {
    width: 295px;
  }
`;

export const HeaderLeft = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
`;

export const MDStatsButton = styled.button`
  padding: 5px 25px;
  background: #e2ae39;
  border-radius: 10px;
  font-size: 24px;
  font-weight: 700;
  border: 0;
  outline: 0;
  box-shadow: none;
  color: #fff;
  margin-top: 20px;
  width: 100%;
  @media (max-width: 768px) {
    width: 328px;
  }
  @media (max-width: 425px) {
    width: 295px;
  }
`;
export const CollectCardRow = styled.div`
  display: flex !important;
  width: 100%;
  flex-wrap: wrap;
  gap: 40px 56px;
  width: 968px;
  margin: 0 auto;

  @media screen and (max-width: 768px) {
    width: 255px;
    gap: 40px 31px;
  }

  @media screen and (max-width: 425px) {
    width: 100%;
    justify-content: space-around;
    gap: 40px 0;
  }
`;

export const CollectionWrapper = styled.div`
  padding-bottom: 40px;
  border-bottom: 1px solid #fff;
  margin-bottom: 50px;

  .policy-id {
    word-break: break-all;
  }

  &:last-of-type {
    border-bottom: none;
  }
`;
