import styled from 'styled-components';

const Image = styled.img`
  width: 200px;
  height: 200px;
  border-radius: 25px;
  margin-bottom: 24px;

  @media screen and (max-width: 768px) {
    width: 112px;
    height: 112px;
  }
`;

const CardText = styled.h6`
  font-size: 28px;
  color: white;
  text-align: center;

  @media screen and (max-width: 768px) {
    font-size: 18px;
  }
  &.italic {
    font-style: italic;
  }
`;

const StatusDot = styled.div`
  margin-left: 5px;
  width: 22px;
  height: 22px;
  min-width: 22px;
  min-height: 22px;
  border-radius: 50%;
  margin-top: 5px;
  background-color: #00ff38;
  &.unstacked {
    background-color: #ff0000;
  }
  @media screen and (max-width: 768px) {
    width: 11px;
    height: 11px;
    min-width: 11px;
    min-height: 11px;
  }
`;

const Wrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 200px;
  @media screen and (max-width: 425px) {
    width: 45%;
  }
`;

interface CollectCardProps {
  src: string;
  name: string;
  rank?: string;
  stackStatus?: boolean;
  amount?: string;
}

const CollectCard = ({ src, name, rank, stackStatus, amount }: CollectCardProps) => {
  return (
    <Wrapper>
      <Image className="card-img" src={src} alt={name} />
      <div className="d-flex">
        <CardText>{name}</CardText>
        <StatusDot className={stackStatus ? '' : 'unstacked'} />
      </div>
      {rank && <CardText>Rank #{rank}</CardText>}
      {amount && <CardText className="italic">Earning {amount} MDT</CardText>}
    </Wrapper>
  );
};

export default CollectCard;
