import styled from 'styled-components';
import { Modal } from 'react-bootstrap';
import { H2, H3, H5, H6, P } from '../common/Typography';
import parse from 'html-react-parser';
import { Fragment } from 'react';

const StyledModal = styled(Modal)`
  padding: 0 !important;
  .modal-dialog {
    max-width: 450px;
    padding: 0 20px;
  }

  .modal-header {
    height: 70px;
    background: #e2ae39;
    padding: 21px;
    font-size: 32px;
    font-weight: 700;
    justify-content: center;
    @media screen and (max-width: 991px) {
      height: 60px;
    }
  }

  .modal-text {
    color: #000;
    font-weight: 600;
    &:not(:last-child) {
      margin-bottom: 32px;
    }
    @media (max-width: 550px) {
      font-size: 24px;
    }
  }
`;

const ContentWrapper = styled.div`
  background: white;
  display: flex;
  align-items: center;
  flex-flow: column;
  padding: 20px;
`;

interface LearnMoreModalProps {
  show: boolean;
  onClose: () => void;
  data?: {
    cars: number;
    dogs: number;
    nfts: number;
    license: number;
  };
}

const MDStatsModal = ({ show, onClose, data }: LearnMoreModalProps) => {
  return (
    <StyledModal show={show} onHide={onClose} centered contentClassName="connect-success-content">
      <Modal.Header>MD STATS</Modal.Header>
      <Modal.Body>
        <ContentWrapper>
          <H6 className="modal-text">Total Assets: {data?.nfts}</H6>
          <H6 className="modal-text">Total Dogs: {data?.dogs}</H6>
          <H6 className="modal-text">Total Cars: {data?.cars}</H6>
          <H6 className="modal-text">Total License: {data?.license}</H6>
        </ContentWrapper>
      </Modal.Body>
    </StyledModal>
  );
};

export default MDStatsModal;
